.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.homeNav {
  width: 40%;
  display: inline-block;
  font-size: 32px;
  margin-left: 5%;
  margin-right: 5%;
}
.homeNav h1 {
  text-align: left;
  font-weight: 200;
}
.laurence {
  letter-spacing: 2px;
}
.bugasu {
  letter-spacing: 14px;
}
#right {
  text-align: right;
}
.homeNav a {
  text-decoration: underline;
  color: black;
  font-size: 28px;
}
.homeNav a:hover {
  text-decoration: line-through;
}
.homeNav a:visited {
  color: black;
}
.frontImage {
  width: 100%;
  text-align: left;
  vertical-align: top;
}
img {
  width: 100%;
}
div.outerBox {
  width: 70%;
  padding: 5%;
  display: inline-block;
  text-align: left;
  vertical-align: top;
}
div.years {
  width: 15%;
  padding-right: 5%;
  display: inline-block;
  text-align: right;
}

h2 {
  text-align: right;
  padding-right: 25%;
  line-height: 500%;
}
.lb {
  font-size: 48px;
  display: inline-block;
  padding: 2.5%;
}
h1.lb {
  border: solid 2px black;
  font-size: 56px;
}
.pageTitle {
  display: inline-block;
  font-size: 32px;
  width: 60%;
  text-align: left;
}
.menu {
  width: 20%;
  display: inline-block;
  text-align: right;
}
img.menuButton {
  width: 30%;
  height: 30%;
  vertical-align: bottom;
}
div.navBar {
  width: 100%;
  display: inline-block;
  border-top: 0;
  margin-top: 0;
}
div.topmost {
  height: 22.5vh;
  padding: 0;
  margin: 0;
  vertical-align: top;
}
div.menuitem {
  width: 25%;
  display: inline-block;
  font-size: 40px;
}
.menuitem a {
  text-decoration: none;
  color: black;
}
hr.menuLines {
  width: 90%;
  height: 2px;
  background-color: black;
  border: none;
}
hr.feat {
  width: 95%;
  height: 1px;
  background-color: black;
  border: none;
}
.footerSection {
  width: 25%;
  display: inline-block;
  font-size: 20px;
  vertical-align: top;
  text-align: left;
}
.footerSection a {
  display: block;
  color: black;
  text-decoration: none;
}
a:hover {
  color: grey;
}
a.lb {
  text-decoration: none;
  color: black;
}
/* h1.lb:hover {
  background-color: black;
} */
hr.footerLines {
  height: 2px;
  background-color: black;
  border: none;
}
.footer {
  margin: 5%;
}
div.content {
  width: 70%;
  display: inline-block;
  vertical-align: top;
}
div.playlist {
  width: 25%;
  display: inline-block;
  border-left: solid 1px black;
  vertical-align: top;
}
h2.playlist {
  padding-left: 15%;
  margin: 0;
  padding-top: 0;
  text-align: left;
}
.playlist iframe {
  padding: 2.5%;
}
.post_image {
  width: 50%;
  display: inline-block;
}
.postContent {
  width: 75%;
  display: inline-block;
  font-size: 20px;
}
.postPreview {
  font-size: 16;
}
a.mainSection {
  font-style: normal;
  color: black;
  text-decoration: none;
}
a.mainSection:hover {
  color: grey;
}
em.subSection {
  font-style: normal;
  color: grey;
}
div.postFrame {
  display: inline-block;
  width: 20%;
  padding: 2.5%;
  vertical-align: top;
}
a.preview {
  text-decoration: none;
  color: black;
}
div.featureContent {
  padding: 2%;
}

a.preview:hover {
  text-decoration: gray;
  color: grey;
}
div.feature {
  padding: 2.5%;
  display: inline-block;
  vertical-align: top;
}

div.featureTitle {
  display: inline-block;
  width: 70%;
}
div.featuredimage {
  width: 25%;
  display: inline-block;
  vertical-align: top;
}

div.posts {
  display: inline-block;
}
h1.featureTitle {
  padding: 0;
  border: 0;
}
h1.feature {
  margin: 0;
}
div.miniFeatureOne {
  width: 45%;
  display: inline-block;
  padding: 1%;
}
div.miniFeatureTwo {
  width: 30%;
  display: inline-block;
  padding: 2.5%;
}
h3.featureTitle {
  padding-left: 15%;
  text-align: left;
  color: #258309;
}
@media only screen and (max-width: 600px){
  .homeNav{
    display: block;
    width: 100%;
  }
  .laurence{
    font-size: 100%;
  }
  #right{
    text-align: center;
  }
  .homeNav h1{
    font-size: 200%;
  }
  .lb {
    width: 90%;
  }
  div.topmost {
    height: 25vh;
    margin: 0;
  }
  h1{
    margin: 0;
    padding: 0;
  }
  .pageTitle{
    font-size: 100%;
  }
  div.postFrame{
    width: 45%;
  }
  .footerSection{
    width: 50%;
  }
  div.menuitem{
    width: 50%;
  }
  div.playlist{
    width: 95%;
    display: block;
  }
  div.content{
    width: 95%;
  }
  
}
.current{
  width: 40%;
  display: inline-block;
  text-align: left;
  padding: 2%;
  vertical-align: top;
}
.current h2{
  text-align: center;
}
#left{
  border-right: black solid 1px;
}